:root {
  --pri-font:"Ubuntu", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --marine-blue: hsl(213, 96%, 18%);
  --purplish-blue: hsl(243, 100%, 62%);
  --light-blue: hsl(206, 94%, 87%);
  --straw-red: hsl(354, 84%, 57%);
  --cool-gray: hsl(231, 11%, 63%);
  --light-gray: hsl(229, 24%, 87%);
  --albaster: hsl(231, 100%, 99%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  list-style: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--pri-font);
}

h1, h2, h3 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.fade-in {
  animation: fade-in 1s ease-in-out forwards;
}

@media (max-width: 63.9375em) {
  body {
    background-color: var(--light-gray);
  }
}

.mobile__nav {
  height: 12.5rem;
  background: url(/assets/images/bg-sidebar-mobile.svg) no-repeat;
  background-size: 100%;
}
.mobile__nav .steps__ctn p {
  color: #fff;
  font-size: 0.95rem;
  font-weight: 700;
  border: 1px solid #fff;
  border-radius: 100%;
  padding: 0.71875rem 1.1875rem;
}
.mobile__nav .steps__ctn .active {
  background-color: var(--light-blue);
  color: #000 !important;
  border: none !important;
}

.desktop__navbar {
  width: 22.35%;
  height: 100vh;
  background: url(/assets/images/bg-sidebar-desktop.svg) no-repeat center;
  background-size: 100%;
}
.desktop__navbar .steps__ctn {
  color: #fff;
}
.desktop__navbar .steps__ctn .step {
  display: flex;
  column-gap: 1.25rem;
}
.desktop__navbar .steps__ctn .step:not(:last-child) {
  margin-bottom: 2.1875rem;
}
.desktop__navbar .steps__ctn .step .active {
  background-color: var(--light-blue);
  color: #000 !important;
  border: none !important;
}
.desktop__navbar .steps__ctn .step .number {
  font-size: 1.2rem;
  font-weight: 700;
  border: 1px solid #fff;
  border-radius: 100%;
  padding: 0.4375rem 1.125rem;
}
.desktop__navbar .steps__ctn .step .step__id {
  color: var(--light-gray);
}
.desktop__navbar .steps__ctn .step .description {
  font-weight: 700;
}

.form {
  background-color: #fff;
  width: 92%;
}
.form__header .description {
  color: var(--cool-gray);
}
.form .errorMsg {
  color: var(--straw-red);
}
.form input {
  border: 1px solid var(--cool-gray);
  border-radius: 0.3125rem;
  transition: all 0.2s ease-in-out;
}
.form input:focus {
  outline: none !important;
  border: 2px solid var(--marine-blue) !important;
}

.btn__ctn {
  bottom: 0 !important;
}
.btn__ctn button {
  background-color: var(--marine-blue) !important;
}

.plans {
  background-color: #fff;
  width: 92%;
}
.plans__header .description {
  color: var(--cool-gray);
}
.plans__ctn .active {
  border: 2px solid var(--purplish-blue);
}
.plans__ctn--card {
  border: 2px solid var(--light-gray);
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}
@media (min-width: 64em) {
  .plans__ctn--card.hover:hover {
    border: 2px solid var(--purplish-blue);
  }
}
.plans__subSwitchCtn {
  background-color: #F8F9FE;
  align-items: center;
}
.plans__subSwitchCtn .monthly {
  color: var(--marine-blue);
}
.plans__subSwitchCtn .yearly {
  color: var(--cool-gray);
}
.plans__subSwitchCtn .switch {
  display: inline-block;
  height: 20px;
  position: relative;
  width: 45px;
}
.plans__subSwitchCtn .switch input {
  display: none;
}
.plans__subSwitchCtn .slider {
  background-color: var(--marine-blue);
  bottom: 0;
  cursor: pointer;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: 0.2s;
}
.plans__subSwitchCtn .slider:before {
  background-color: #fff;
  bottom: 2.6px;
  content: "";
  height: 15px;
  width: 15px;
  left: 4px;
  position: absolute;
  transition: 0.2s;
}
.plans__subSwitchCtn input:checked + .slider {
  background-color: var(--marine-blue);
}
.plans__subSwitchCtn input:checked + .slider:before {
  transform: translateX(20px);
}
.plans__subSwitchCtn .slider.round {
  border-radius: 34px;
}
.plans__subSwitchCtn .slider.round:before {
  border-radius: 50%;
}

.btn__ctn .goBackBtn {
  background: none !important;
  color: var(--cool-gray);
  transition: all 0.2s ease-in-out;
}
.btn__ctn .goBackBtn:hover {
  color: var(--marine-blue);
}

.g {
  color: var(--cool-gray) !important;
}

.blue {
  color: var(--marine-blue) !important;
}

.addOns__header .description {
  color: var(--cool-gray);
}
.addOns__ctn {
  /* Hide the browser's default checkbox */
  /* Create a custom checkbox */
  /* On mouse-over, add a grey background color */
  /* When the checkbox is checked, add a blue background */
  /* Create the checkmark/indicator (hidden when not checked) */
  /* Show the checkmark when checked */
  /* Style the checkmark/indicator */
}
.addOns__ctn .active {
  border: 2px solid var(--purplish-blue);
}
.addOns__ctn .container {
  position: relative;
  cursor: pointer;
}
.addOns__ctn .container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.addOns__ctn .checkmark {
  position: absolute;
  top: 50%;
  transform: translate(50%, -50%);
  height: 20px;
  width: 20px;
  border-radius: 5px;
  background-color: #fff;
  border: 1px solid var(--light-gray);
}
.addOns__ctn .container:hover input ~ .checkmark {
  background-color: #fefefe;
}
.addOns__ctn .container input:checked ~ .checkmark {
  background-color: var(--purplish-blue);
  border: none;
}
.addOns__ctn .checkmark:after {
  content: "";
  position: absolute;
  display: none;
}
.addOns__ctn .container input:checked ~ .checkmark:after {
  display: block;
}
.addOns__ctn .container .checkmark:after {
  left: 7px;
  top: 3px;
  width: 6px;
  height: 12px;
  border: solid white;
  border-width: 0 3px 4px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
.addOns__ctn--card {
  border: 2px solid var(--light-gray);
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}
@media (min-width: 64em) {
  .addOns__ctn--card.hover:hover {
    border: 2px solid var(--purplish-blue);
  }
}
.addOns__ctn--card__priceCtn .price {
  color: var(--purplish-blue);
}

.finish .g {
  color: var(--cool-gray);
}
.finish__header .description {
  color: var(--cool-gray);
}
.finish__ctn {
  background-color: #F8F9FE;
  max-width: 31.875rem;
}
.finish__ctn--left p, .finish__ctn--right p {
  padding: 0.625rem 0 0.625rem 0;
}
.finish__totalCtn {
  max-width: 32.5rem;
}
.finish__totalCtn .totalWriteUp {
  color: var(--cool-gray);
}

